export const demoLibrary = [{"title":"Программист-фанатик","author":"Чед Фаулер","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/programmist-fanatik.png?itok=6q1YIkzx","description":"В этой книге вы не найдете описания конкретных технологий, алгоритмов\\nи языков программирования – ценность ее не в этом. Она представляет\\nсобой сборник практических советов и рекомендаций, касающихся\\nситуаций, с которыми порой сталкивается любой разработчик: отсутствие\\nмотивации, выбор приоритетов, психология программирования, отношения\\nс руководством и коллегами и многих других. Подобные знания обычно\\nприходят лишь в результате многолетнего опыта реальной работы. По\\nбольшому счёту перед вами – ярко и увлекательно написанное руководство,\\nкоторое поможет быстро сделать карьеру в индустрии разработки ПО любому,\\nкто поставил себе такую цель. Конечно, опытные программисты могут найти\\nнекоторые идеи автора достаточно очевидными, но и для таких найдутся\\nтемы, которые позволят пересмотреть устоявшиеся взгляды и выйти на новый\\nуровень мастерства. Для тех же, кто только в самом начале своего пути как\\nразработчика, чтение данной книги, несомненно, откроет широчайшие\\nперспективы.","pageQnt":"158","id":1633418892921,"comments":[{"name":"Роберт Мартин","text":"Not bad!","note":4,"id":1633419684061},{"name":"Чарльз Петцольд","text":"Perfect!!!","note":5,"id":1633419710926}],"rate":"4.5"},{"title":"Идеальный программист","author":"Роберт Мартин","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/idealnyy-programmist.jpg?itok=ponxEaDm","description":"Всех программистов, которые добиваются успеха в мире разработки ПО,\\nотличает один общий признак: они больше всего заботятся о качестве\\nсоздаваемого программного обеспечения. Это – основа для них. Потому\\nчто они являются профессионалами своего дела. В этой книге легендарный\\nэксперт Роберт Мартин (более известный в сообществе как «Дядюшка Боб»),\\nавтор бестселлера «Чистый код», рассказывает о том, что значит «быть\\nпрофессиональным программистом», описывая методы, инструменты и\\nподходы для разработки «идеального ПО». Книга насыщена практическими\\nсоветами в отношении всех аспектов программирования: от оценки проекта\\nи написания кода до рефакторинга и тестирования. Эта книга – больше, чем\\nописание методов, она о профессиональном подходе к процессу разработки.","pageQnt":"186","id":1633418987073,"comments":[{"name":"Чед Фаулер","text":"Nice!","note":5,"id":1633419959169},{"name":"Стив Макконнелл","text":"Can be better!","note":3,"id":1633420049443}],"rate":"4.0"},{"title":"Код. Тайный язык","author":"Чарльз Петцольд","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/kod-taynyy-yazyk-informatiki_0.png?itok=kc8ExhSO","description":"Книга «Код» представляет собой увлекательное путешествие в прошлое – мир\\nэлектрических устройств и телеграфных машин. Знакомство с прообразами\\nпервых компьютеров позволит читателю с любым уровнем технической\\nподготовки узнать о том, как работают современные электронные устройства.","pageQnt":"134","id":1633419082070,"comments":[{"name":"Роберт Мартин","text":"Very bad!!!","note":2,"id":1633420096023}],"rate":"2.0"},{"title":"Совершенный КОД","author":"Стив Макконнелл","cover":"https://monster-book.com/sites/default/files/styles/sc290x400/public/books/sovershennyy-kod.jpg?itok=91Cf3Ohw","description":"Более 10 лет первое издание этой книги считалось одним из лучших практических руководств по программированию. Сейчас эта книга полностью обновлена с учетом современных тенденций и технологий и дополнена сотнями\\nновых примеров, иллюстрирующих искусство и науку программирования. Опираясь на академические исследования, с одной стороны, и практический опыт\\nкоммерческих разработок ПО — с другой, автор синтезировал из самых эффективных методик и наиболее эффективных принципов ясное прагматичное руководство. Каков бы ни был ваш профессиональный уровень, с какими бы средствами разработки вы ни работали, какова бы ни была сложность вашего проекта,\\nв этой книге вы найдете нужную информацию, она заставит вас размышлять\\nи поможет создать совершенный код.","pageQnt":"247","id":1633419148045,"comments":[]}]



